import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
import Json from '@/Json';
export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			tabCurrentIndex: 0,
			navList: [
				{state: 0,text: '全部',loadingType: 'more',orderList: [],pn:0},
				{state: 1,text: '待付款',loadingType: 'more',orderList: [],pn:0},
				{state: 2,text: '待发货',loadingType: 'more',orderList: [],pn:0},
				{state: 3,text: '待收货',loadingType: 'more',orderList: [],pn:0},
				{state: 4,text: '已完成',loadingType: 'more',orderList: [],pn:0}
			],
		};
	},
	
	onLoad(options){
		this.tabCurrentIndex = +options.state;
	},
	onShow() {
		this.loadData('refresh')
	},
	methods: {
		goDetail(id){
			uni.navigateTo({
				url:"/pages/order/detail?id="+id
			})
		},
		navTo(url){
			uni.navigateTo({
				url:url
			})
		},
		goDelivery(id){
			uni.navigateTo({
				url:"/pages/order/delivery?id="+id
			})
		},
		zaicimai(arr){
			uni.setStorageSync('buyarr',arr);
			var url = "/pages/order/createOrder";
			uni.navigateTo({
				url:url
			})
		},
		//获取订单列表
		loadData(source){
			//这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let state = navItem.state;
			let pn = navItem.pn;
			if(source === 'tabChange' && navItem.loaded === true){				
				//tab切换只有第一次需要加载数据
				return;
			}
			if(navItem.loadingType == 'loading' || navItem.loadingType == 'noMore'){
				return;
			}
			if(pn==0){
				uni.showLoading({
					title: '请稍后'
				})
			}
			navItem.loadingType = 'loading';
			uni.request({
				url:uni.getStorageSync('hostname') + '?action=index/order/index',
				data:{order_status:state,token:uni.getStorageSync('cbe_token'),pn:pn},
				method:'POST',
				dataType:'json',
				header: {'content-type' : 'application/x-www-form-urlencoded'},
				success: (resa) => {						
					var datas = resa.data;
					uni.hideLoading();
					if(datas.code == '0'){	
						this.navList[index].pn = Number(pn)+Number(1);
						datas.data.forEach(item=>{
							navItem.orderList.push(item);
						})
						navItem.loadingType = 'loaded';						
					}else{
						this.navList[index].loadingType = "noMore"
					}
				},
			
			})
			return false;
			setTimeout(()=>{
				let orderList = Json.orderList.filter(item=>{
					//添加不同状态下订单的表现形式
					item = Object.assign(item, this.orderStateExp(item.state));
					//演示数据所以自己进行状态筛选
					if(state === 0){
						//0为全部订单
						return item;
					}
					return item.state === state
				});
				orderList.forEach(item=>{
					navItem.orderList.push(item);
				})
				//loaded新字段用于表示数据加载完毕，如果为空可以显示空白页
				this.$set(navItem, 'loaded', true);	
				//判断是否还有数据， 有改为 more， 没有改为noMore 
				navItem.loadingType = 'more';
			}, 600);	
		}, 

		//swiper 切换
		changeTab(e){
			this.tabCurrentIndex = e.target.current;
			this.loadData('tabChange');
		},
		//顶部tab点击
		tabClick(index){
			this.tabCurrentIndex = index;
		},
		//删除订单
		deleteOrder(index,item){
			var that = this;
			uni.showModal({
			    content: '确定删除该订单吗？',
				confirmColor:'#008AB0',
			    success: (e)=>{
			    	if(e.confirm){
						uni.showLoading({
							title: '请稍后'
						})
						this.shanchuOrder(item);
						setTimeout(()=>{
							this.navList[this.tabCurrentIndex].orderList.splice(index, 1);
							uni.hideLoading();
						}, 600)
					}
				},
			})
		},
		//取消订单
		cancelOrder(item){
			var that = this;
			uni.showModal({
			    content: '确定取消该订单吗？',
				confirmColor:'#008AB0',
			    success: (e)=>{
			    	if(e.confirm){
						uni.showLoading({
							title: '请稍后'
						})
						that.quxiaoOrder(item);
						setTimeout(()=>{
							let {stateTip, stateTipColor} = that.orderStateExp(9);
							item = Object.assign(item, {
								state: 9,
								stateTip, 
								stateTipColor
							})
							//取消订单后删除待付款中该项
							let list = that.navList[1].orderList;
							let index = list.findIndex(val=>val.id === item.id);
							index !== -1 && list.splice(index, 1);
							uni.hideLoading();
						}, 600)
					}
				},
			})
		},
		quxiaoOrder(item){
			if(item.status==0){
				uni.request({
					url:uni.getStorageSync('hostname') + '?action=index/order/order_quxiao',
					data:{id:item.ordersn,token:uni.getStorageSync('cbe_token')},
					method:'POST',
					dataType:'json',
					header: {'content-type' : 'application/x-www-form-urlencoded'},
					success: (resa) => {
						var one = resa.data;
						if(one.code==0){
							
						}
					}
				})
			}
		},
		shouhuo(item){
			var that = this;
			uni.showModal({
			    content: '确定已经收货了吗？',
				confirmColor:'#008AB0',
			    success: (e)=>{
			    	if(e.confirm){
						uni.showLoading({
							title: '请稍后'
						})
						uni.request({
							url:uni.getStorageSync('hostname') + '?action=index/order/order_shouhuo',
							data:{id:item.ordersn,token:uni.getStorageSync('cbe_token')},
							method:'POST',
							dataType:'json',
							header: {'content-type' : 'application/x-www-form-urlencoded'},
							success: (resa) => {
								uni.hideLoading();
								var one = resa.data;
								if(one.code==0){
									that.loadData();
									setTimeout(()=>{
										let {stateTip, stateTipColor} = that.orderStateExp(4);
										item = Object.assign(item, {
											state: 4,
											stateTip, 
											stateTipColor
										})
										//取消订单后删除待付款中该项
										let list = that.navList[1].orderList;
										let index = list.findIndex(val=>val.id === item.id);
										index !== -1 && list.splice(index, 1);
										uni.hideLoading();
									}, 600)
								}
							}
						})
			    	}
			    }
			});
		},
		shanchuOrder(item){
			if(item.status==-1){
				uni.request({
					url:uni.getStorageSync('hostname') + '?action=index/order/order_shanchu',
					data:{id:item.ordersn,token:uni.getStorageSync('cbe_token')},
					method:'POST',
					dataType:'json',
					header: {'content-type' : 'application/x-www-form-urlencoded'},
					success: (resa) => {
						var one = resa.data;
						if(one.code==0){
							
						}
					}
				})
			}
		},
		//订单状态文字和颜色
		orderStateExp(state){
			let stateTip = '',
				stateTipColor = '#37b0c9';
			switch(+state){
				case 1:
					stateTip = '待付款'; break;
				case 2:
					stateTip = '待发货'; break;
				case 3:
					stateTip = '待收货'; break;
				case 4:
					stateTip = '已完成'; break;
				case 9:
					stateTip = '已取消'; 
					stateTipColor = '#909399';
				break;
			}
			return {stateTip, stateTipColor};
		}
	},
}
